import { Query } from '@ali/4ever-cangjie';
import isSub from '../queries/isSub';
import isSup from '../queries/isSup';
import * as actions from '../actions';
import onAction from '../handlers/onAction';
import onKeyDown from '../handlers/onKeyDown';
import createOnCangjieInput from '../handlers/onCangjieInput';
import { moFactory } from '@ali/4ever-plugin-vert-align';

const queries: Record<string, Query> = {
  isSub,
  isSup,
};

function createVertAlignPlugin() {
  return {
    ...moFactory({}),
    queries,
    onAction,
    onKeyDown,
    onCangjieInput: createOnCangjieInput(),
    models: {
      klass: 'mark',
      type: 'vertAlign',
    },
  };
}

createVertAlignPlugin.actions = actions;
createVertAlignPlugin.queries = queries;

export default createVertAlignPlugin;
